<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <a-alert
                message="功能提示"
                description="发送给登录用户的留言，只可以浏览。"
                type="info"
                showIcon
        />
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                :showHeader="true"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
    </div>
</template>

<script>
    import TopButtons from '../留言列表我发起的/components/TopButtons'
    import {getAboutMeCommentList, deleteComment} from '@/api/comment'
    import tableComponent from '@/components/table'

    export default {
        components: {
            TopButtons, tableComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '标题',
                        dataIndex: 'comment_title',
                        key: 'comment_title',
                    },
                    {
                        title: '留言人',
                        dataIndex: 'user_name',
                        key: 'user_name',
                    },
                    {
                        title: '更新时间',
                        dataIndex: 'updated_at',
                        key: 'updated_at',
                    },
                    {
                        title: '留言时间',
                        dataIndex: 'leave_at',
                        key: 'leave_at',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {visible: true, disable: false,},
                        }
                    }
                ],
                tableRequest: getAboutMeCommentList,
            }
        },
        methods: {
            handleOperate(type, row) {
                let id = row.id;
                switch (type) {
                    case 'check':
                        this.$router.push({name: '留言查看', params: {id: id}})
                        break;
                    case 'edit':
                        this.$router.push({name: '留言编辑', params: {id: id}})
                        break;
                    case 'delete':
                        deleteComment(id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;

                }
            },
        },
    }
</script>
